<template>
    <div class="col-md-2 side-nav rounded-4" :class="{ in: !isSideNav, out: isSideNav}">
        <nav class="nav flex-column my-3" style="position: relative;">
          <form class="d-flex" role="search">
            <input class="form-control me-2" type="search" :placeholder="text.search" aria-label="Search" v-model="searchFilter">
          </form>
          <div class="me-2 mb-3">
            <select class="form-select mt-2" v-model="elementFilter" @click="clearSearch">
              <option selected :value="text.element">{{ text.element }}</option>
              <option v-for="item in text.elementList" :key="item" :value="item" v-text="item"></option>
            </select>
            <select class="form-select mt-2" v-model="classFilter" @click="clearSearch">
              <option selected :value="text.class">{{ text.class }}</option>
              <option v-for="item in text.classList" :key="item" :value="item" v-text="item"></option>
            </select>
          </div>
          <div id="nav-scroll">
            <router-link 
              v-for="item in unitsFilter" :key="item.Name[language]"
              class="btn btn-outline-secondary nav-link my-1 mx-3 me-md-4"
              @mouseover="showIcon(item)"
              @mouseleave="hideIcon()"
              @click="initialUnit(item.Cname)"
              :to="{ 'name': 'heroName', 'params': { heroName: item.Name[0] }}"
              >
              {{ item.Name[language] }}
            </router-link>
          </div>
          <div style="position: absolute; top: 50%; left: 100%; width: 120%;">
            <img :src="selectIcon" alt="" style="width: 30%;" v-show="isSelectIcon">
          </div>
        </nav>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, watch } from 'vue'
import { computed } from '@vue/runtime-core'
import { storeToRefs } from 'pinia'
import { useMobile } from '@/store/Mobile'
import { useLanguage } from '@/store/Language'
import { useInitUnit } from '@/store/InitUnit'
import Characters from '@/assets/json/Characters.json'

export default {
    name: 'SideNav',
    setup() {
        const { isSideNav } = storeToRefs(useMobile())
        const { language, text } = storeToRefs(useLanguage())
        const classList = ref(text.value.classList)
        const elementList = ref(text.value.elementList)

        const {
          unit,
          searchFilter,
          classFilter,
          elementFilter,
          clearSearch,
        } = storeToRefs(useInitUnit())

        classFilter.value = text.value.class
        elementFilter.value = text.value.element

        watch(language, () => {
          classFilter.value = text.value.class
          elementFilter.value = text.value.element
        })
        const selectIcon = ref('')
        const isSelectIcon = ref(false)
        const showIcon = (item) => {
          isSelectIcon.value = true
          selectIcon.value = `img/UI_Card_S/${item.Imgs[0]}`
        }

        const hideIcon = () => {
          isSelectIcon.value = false
          selectIcon.value = ''
        }
        const unitsFilter = computed(() => {
          return Characters.filter((item) => {
            if (searchFilter.value) {
              classFilter.value = text.value.class
              elementFilter.value = text.value.element
            }

            return ((classFilter.value === item.Class[language.value]) || (classFilter.value === text.value.class))
            && ((elementFilter.value === item.Element[language.value]) || (elementFilter.value === text.value.element))
            && (item.Name[language.value].toLowerCase().includes(searchFilter.value.toLowerCase()))
          })
        })

        const initialUnit = (Cname) => {
          // 選擇角色並縮回側欄
          $(".carousel-inner").carousel({   
              pause: true,
              interval: false
          }).carousel(0);
          useInitUnit().initialUnit(Cname)
          useMobile().toggleSideNav()
        }
        return {
            language,
            isSideNav,
            classList,
            elementList,
            text,
            selectIcon,
            isSelectIcon,
            showIcon,
            hideIcon,
            searchFilter,
            elementFilter,
            classFilter,
            clearSearch,
            unitsFilter,
            initialUnit
        }
    }
}
</script>

<style scoped>
.nav-link {
    color: black;
}
#nav-scroll {
    overflow-y: scroll; 
    overflow-x: hidden; 
    height: 100vh;
}

@media screen and (max-width: 768px) {
    .side-nav{
        position:fixed;
        overflow:hidden;
        background-color: rgba(255, 255, 255, 0.9);
        z-index: 10;
        width:100%;
        left:-100%;
        transition:left 1.5s;
    }
    .side-nav.out{
        left:0;
    }
    .side-nav.in {
        left:-100%;
    }
    #nav-scroll {
        height: 55vh;
    }
}
</style>