<template>
  <div :class="{'mt-5 pt-5':isMobile}">
    <TopNav/>
    <router-view/>
    <BottomFooter/>
  </div>
</template>

<script>
/* eslint-disable */
import TopNav from '@/components/TopNav.vue'
import BottomFooter from '@/components/BottomFooter.vue'
import { useRoute, useRouter } from 'vue-router'
import { useMobile } from "@/store/Mobile"
import { useLanguage } from '@/store/Language'
import { useInitUnit } from '@/store/InitUnit'
import { storeToRefs } from 'pinia'

export default {
  name: 'App',
  components: {
    TopNav,
    BottomFooter,
  },
  setup() {
    const { language } = storeToRefs(useLanguage())
    if (useRoute().query.lang != undefined) {
      language.value = Math.max(0, ['en', 'ko', 'ja', 'zh'].indexOf(useRoute().query.lang))
    }

    const { 
      isMobile,
      screenTop,
      touchstartX,
      touchstartY,
      touchendX,
      touchendY
    } = storeToRefs(useMobile())

    document.addEventListener('touchstart', function (event) {
        screenTop.value = event.view.scrollY
        touchstartX.value = event.changedTouches[0].screenX
        touchstartY.value = event.changedTouches[0].screenY
    }, false);

    document.addEventListener('touchend', function (event) {
        touchendX.value = event.changedTouches[0].screenX
        touchendY.value = event.changedTouches[0].screenY
        useMobile().swipe()
    }, false);

    return {
      isMobile
    }
  }
}
</script>

<style>
body {
  font-family: sans-serif;
}
</style>
